.contact__container{

    width:58%;
    display: grid;
    grid-template-columns: 30% 58% ;
    gap: 12rem;
}
.contact__options{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}

.contact__option{
    background:rgb(10, 163, 223);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);

}

.contact__option:hover{
    background: transparent;
    border-color:rgb(10, 163, 223); ;
}

.contact__option-icon{
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.contact__option a{
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}

form{
    display: flex;
flex-direction: column;
gap: 1.2rem;

}

input,textarea{
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid  rgb(10, 163, 223);
    border-color:rgb(10, 163, 223);
    resize: none;
    color: honeydew;
} 


@media screen and (max-width: 1024px) {
    .contact__container{
        grid-template-columns: 1fr;
        gap: 2rem;
    } 
}

@media screen and (max-width: 600px) {
    .contact__container{
        width:var(--container-width-ms);
        
    }
}